}

/* make the cards look like ubttons
 * Path: Travel.css */
.card {
    cursor: pointer;
    border: 10px;
    border-radius: 10px 10px 0 0;
    border-color: black;
}

/* Add a hover effect to the cards */
.card-container:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s all;
}

